
import XInput from 'vux/src/components/x-input/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'


import { showAlertAuto } from '@/utils/index'

let login = {
name: 'login',
data () {
return {
userinfo: {
username: '',
password: ''
},
position: 'default',
showToast: false
}
},
components: {
XInput,
Box,
Group,
XButton
},
mounted () {
this.userinfo.username = cookie.get('username')
},
methods: {
handlelogin () {
window.document.addEventListener('message', (e) => {
if (typeof (e.data) === 'string') {
let dataArr = e.data.split(':')
if (dataArr[0] === 'offlineloginresponse') {
let userinfo = e.data.substring(dataArr[0].length + 1)
this.offlinelogin(userinfo)
}
}
})
if (this.userinfo.username === '' || this.userinfo.password === '') {
this.position = 'middle'
showAlertAuto('提示！', '用户名/密码不能为空！')
} else {
this.$store.commit('UPDATE_LOADING_STATUS', true)
window.postMessage('offlinelogin:' + JSON.stringify(this.userinfo))
}
},
offlinelogin (info) {
if (info === 'fail') {
showAlertAuto('提示！', '登录失败!')
this.$store.commit('UPDATE_LOADING_STATUS', false)
} else {
info = JSON.parse(info)
this.$store.commit('UPDATE_LOGIN', true)
this.$store.commit('SET_USER_ID', info.userid)
this.$store.commit('SET_USER_NAME', info.realname)
this.$store.commit('SET_USER_ROLES', info.rolelist)
cookie.set('username', info.username)
window.localStorage.setItem('username', info.username)
this.$router.push({path: '/index/mytask', query: {username: info.username}})
}
}
}
}
export default login



